.popup {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    display: flex;
    opacity: 0;
    transition: opacity 0.2s linear, visibility 0.2s linear;
}
