.form-auth__submit-button {
    width: 100%;
    min-height: 50px;
    background-color: #fff;
    margin-top: 210px;
    border: 0;
    border-radius: 2px;
    padding: 0;
    color: #000;
    cursor: pointer;
    font-family: 'Inter', 'Arial', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.21;  
  }
  
  .form-auth__submit-button:hover {
    background-color: rgba(255, 255, 255, 0.8);
  }