.info-popup__container {
    display: flex;
    flex-direction: column;
    max-width: 430px;
    max-height: 330px;
    width: 100%;
    position: relative;
    background-color: #fff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    cursor: default;
    padding: 60px 36px 60px 36px;
    box-sizing: border-box;
  }