.form-auth__text {
    width: 100%;
    min-height: 27px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.21;
    box-sizing: border-box;
    border: 0;  
    padding: 0 0 10px 0;
    background-color: #000;
    color: #fff;
    border-bottom: 2px solid rgba(204, 204, 204, 0.5);
  }
  
  .form-auth__text:focus {
    outline: none;
    border-bottom: 2px solid rgba(255, 255, 255);
  }
  
  .form-auth__text:nth-of-type(2) {
    margin-bottom: 0;
  }
  
  ::placeholder { 
    color: #ccc;  
  }